<!-- Dati Antropometrici -->
<template>
  <v-form v-model="valid" ref="form">
      <v-row>
	<v-col cols="12" md="12">
                <v-select v-model="skinType" :items="skinOptions" :rules="[v => skinOptions.includes(v) || 'Item is required']" label="Skin" required></v-select>
        </v-col>
      </v-row>
  </v-form>
</template>

<script>
import { mapState } from 'vuex';
import loadFromStore from "../mixins/loadFromStore.js"
  
export default {
    mixins: [loadFromStore],
    data: () => ({
	valid: false,
	skinType: null,
	skinOptions: ['secca', 'rughe fisse', 'rughe di espressione' , 'macchie', 'normale' , 'pelle grassa',],
    }),
    methods: {
	validate() {
            if (this.$refs.form.validate()) {
		let result = (({ skinType }) => ({ skinType }))(this);
		this.$store.commit('setSkin', result);
            }
	},
    },
    computed: {
	    ...mapState(['skin']),
    },
    mounted() {
	// Loading skin values from vuex if already present
	this.loadFromStore(this.$data, this.skin);
    },
}
</script>
